.alignnone {
  margin: 5px 20px 20px 0;
}
.aligncenter,
div.aligncenter {
  display: block;
  margin: 5px auto 5px auto;
}
.alignright {
  float: right;
  margin: 5px 0 20px 20px;
}
.alignleft {
  float: left;
  margin: 5px 20px 20px 0;
}
a {
  img {
    &.alignright {
      float: right;
      margin: 5px 0 20px 20px;
    }
    &.alignnone {
      margin: 5px 20px 20px 0;
    }
    &.alignleft {
      float: left;
      margin: 5px 20px 20px 0;
    }
    &.aligncenter {
      display: block;
      margin-left: auto;
      margin-right: auto;
    }
  }
}
.wp-caption {
  max-width: 96%;
  padding: 5px 3px 10px;
  text-align: center;
  &.alignnone {
    margin: 5px 20px 20px 0;
  }
  &.alignleft {
    margin: 5px 20px 20px 0;
  }
  &.alignright {
    margin: 5px 0 20px 20px;
  }
  img {
    border: 0 none;
    height: auto;
    margin: 0;
    max-width: 98.5%;
    padding: 0;
    width: auto;
  }
  p {
    &.wp-caption-text {
      font-size: 11px;
      line-height: 17px;
      margin: 0;
      padding: 0 4px 5px;
    }
  }
}
.gallery-item {
  display: inline-block;
  text-align: left;
  vertical-align: top;
  width: 50%;
  a {
    img {
      display: block;
      -webkit-transition: -webkit-filter 0.2s ease-in;
      transition: -webkit-filter 0.2s ease-in;
      transition: filter 0.2s ease-in;
      transition: filter 0.2s ease-in, -webkit-filter 0.2s ease-in;
      -webkit-backface-visibility: hidden;
      backface-visibility: hidden;
    }
  }
}
.gallery-item a,
.gallery-item a:hover,
.gallery-item a:focus {
  -webkit-box-shadow: none;
  box-shadow: none;
  background: none;
  display: inline-block;
  max-width: 100%;
}
.gallery-item a:hover img,
.gallery-item a:focus img {
  -webkit-filter: opacity(60%);
  filter: opacity(60%);
}
.gallery-caption {
  display: block;
  text-align: left;
  padding: 0 10px 0 0;
  margin-bottom: 0;
}
.gallery-columns-1 {
  .gallery-item {
    max-width: 100%;
  }
}
.gallery-columns-2 {
  .gallery-item {
    max-width: 50%;
  }
}
.gallery-columns-3 {
  .gallery-item {
    max-width: 33%;
  }
}
.gallery-columns-4 {
  .gallery-item {
    max-width: 25%;
  }
}
.gallery-columns-5 {
  .gallery-item {
    max-width: 20%;
  }
}
.gallery-columns-6 {
  .gallery-item {
    max-width: 16.66%;
  }
}
.gallery-columns-7 {
  .gallery-item {
    max-width: 14.28%;
  }
}
.gallery-columns-8 {
  .gallery-item {
    max-width: 12.5%;
  }
}
.gallery-columns-9 {
  .gallery-item {
    max-width: 11.11%;
  }
}
.gallery-columns-6 .gallery-caption,
.gallery-columns-7 .gallery-caption,
.gallery-columns-8 .gallery-caption,
.gallery-columns-9 .gallery-caption {
  display: none;
}
.bypostauthor {
  font-weight: bold;
}
.screen-reader-text {
  clip: rect(1px, 1px, 1px, 1px);
  position: absolute !important;
  height: 1px;
  width: 1px;
  overflow: hidden;
  &:focus {
    background-color: #f1f1f1;
    border-radius: 3px;
    box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.6);
    clip: auto !important;
    color: #21759b;
    display: block;
    font-size: 14px;
    font-size: 0.875rem;
    font-weight: bold;
    height: auto;
    left: 5px;
    line-height: normal;
    padding: 15px 23px 14px;
    text-decoration: none;
    top: 5px;
    width: auto;
    z-index: 100000;
  }
}
